.nav {
  position: fixed;
  top: 0;
  width: 100%;
  padding: 20px;
  height: 40px;
  z-index: 1;

  /* animation */
  transition-timing-function: ease-in;
  transition: all 0.5s;
}

.nav__black {
  background-color: black;
}

.nav__logo {
  position: fixed;
  left: 20px;
  top: 10px;
  width: 120px;
  object-fit: contain;
}

.nav__avatar {
  position: fixed;
  right: 20px;
  width: 30px;
  object-fit: contain;
}
